import React from "react";
import "../css/card.css";
import ProfileIcon from "../assets/profile-icon.jpg";

const Card = (props) => {
  return (
    <div className="card-box">
      <img className="profile-icon" src={ProfileIcon} alt="" />
      <div id="nameid-label">
        <p>
          Username: <span>{props.emp.Name}</span>
        </p>
        <p>
          Id no: <span>{props.emp.EmployeeID}</span>
        </p>
      </div>
      <div>
        <button onClick={() => props.btnfn()} id="view-btn">
          {props.btntext}
        </button>
      </div>
    </div>
  );
};

export default Card;
