import React from "react";
import "../css/calculator.css";

const Calculator = () => {
  const [value, setvalue] = React.useState("");
  return (
    <div class="calculator">
      <input type="text" value={value} id="display" readonly></input>
      <div class="buttons">
        <button class="button" onClick={() => setvalue(value + "9")}>
          9
        </button>
        <button class="button" onClick={() => setvalue(value + "8")}>
          8
        </button>
        <button class="button" onClick={() => setvalue(value + "7")}>
          7
        </button>
        <button class="button" onclick="appendToDisplay('c')">
          c
        </button>
        <button class="button" onClick={() => setvalue(value + "6")}>
          6
        </button>
        <button class="button" onClick={() => setvalue(value + "5")}>
          5
        </button>
        <button class="button" onClick={() => setvalue(value + "4")}>
          4
        </button>
        <button class="button" onClick={() => setvalue(value + "+")}>
          +
        </button>
        <button class="button" onClick={() => setvalue(value + "3")}>
          3
        </button>
        <button class="button" onClick={() => setvalue(value + "2")}>
          2
        </button>
        <button class="button" onClick={() => setvalue(value + "1")}>
          1
        </button>
        <button class="button" onClick={() => setvalue(value + "-")}>
          -
        </button>
        <button class="button" onClick={() => setvalue(value + "0")}>
          0
        </button>
        <button class="button" onClick={() => setvalue(value + "00")}>
          00
        </button>
        <button class="button" onClick={() => setvalue("")}>
          AC
        </button>
        <button class="button" onclick="appendToDisplay('*')">
          *
        </button>

        <button class="button" onClick={() => setvalue(value + "/")}>
          /
        </button>
        <button class="button" onClick={() => setvalue(eval(value))}>
          =
        </button>
      </div>
    </div>
  );
};

export default Calculator;
