import React from "react";
import "../css/navbar.css";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <nav>
      <ul>
        <li>
          <NavLink to="/Homepage">
            <i class="fa-solid fa-house-chimney-user fa-xs"></i> Home page
          </NavLink>
        </li>
        <li>
          <NavLink to="/Signup">
            <i class="fa-solid fa-user-large"></i> Signup
          </NavLink>
        </li>
        <li>
          <NavLink to="/Login">
            <i class="fa-solid fa-arrow-right-from-bracket"></i> Login
          </NavLink>
        </li>
        <li>
          <NavLink to="/Calculator">
            <i class="fa-solid fa-calculator"></i> Calculator
          </NavLink>
        </li>
        <li>
          <NavLink to="/Employlist">
            <i class="fa-regular fa-address-book"></i> Employlist
          </NavLink>
        </li>

        <li>
          <NavLink to="/pagination">
            <i class="fa-regular fa-address-book"></i> pagination
          </NavLink>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;
